







































































































// @ts-ignore
import vueSlider from 'vue-slider-component';
import {Component, Vue} from 'vue-property-decorator';
import HistoryTableView from '@/views/dice/diceHistoryTableView.vue';
import Loading from '@/components/loading.vue';
import {toFixed} from '../../modules/utils/commonFunctions';
import {DiceController, DiceEffectCallback} from '../../modules/controllers/diceController';

@Component({
    components: {
        HistoryTableView,
        vueSlider,
        Loading,
    },
})
export default class DiceView extends Vue implements DiceEffectCallback {
    public readonly control: DiceController = new DiceController(this);

    public balanceDelta: number = 0;
    public balanceTicker: any = null;
    public balanceEffectVisible: boolean = false;
    public balanceEffectStyle: any = {'color': 'green', 'margin-left': '10 px'};

    public underSlide: any = {
        roll: this.control.betNumber,
        height: 12,
        dotWidth: 20,
        dotHeight: 24,
        min: 2,
        max: 97,
        speed: 0.1,
        tooltip: 'hover',
        bgStyle: {
            backgroundColor: 'transparent',
            border: '1px solid #938282',
        },
        tooltipStyle: {
            backgroundColor: '#666',
            borderColor: '#666',
        },
        processStyle: {
            backgroundColor: '#0DD985',
            margin: '-1px',
            height: '12px',
        },
    };

    public overSlide: any = {
        roll: this.control.betNumber,
        height: 12,
        dotWidth: 20,
        dotHeight: 24,
        min: 2,
        max: 97,
        speed: 0.1,
        tooltip: 'hover',
        bgStyle: {
            backgroundColor: '#0DD985',
            marginTop: '0px',
            height: '12px',
        },
        tooltipStyle: {
            backgroundColor: '#666',
            borderColor: '#666',
        },
        processStyle: {
            backgroundColor: '#230304',
            border: '1px solid #938282',
            boxShadow: '0 0 20px #230304',
        },
    };

    public mounted(): void {
        this.control.initialize();
    }

    public beforeDestroy(): void {
        this.control.destroy();
    }

    public get rollStyle(): any {
        if (this.control.isRollUnder) {
            return parseInt(this.control.rollValue, 10) < this.control.betNumber
                    ? {color: '#0dd985'} : {color: '#9f0000'};
        } else {
            return parseInt(this.control.rollValue, 10) > this.control.betNumber
                    ? {color: '#0dd985'} : {color: '#9f0000'};
        }
    }

    public get top(): string {
        return -this.control.activeIndex * 45 + 'px';
    }

    public get transition(): string {
        return this.control.activeIndex === 0 ? 'none' : 'top 0.5s';
    }


    public startBalanceEffect(delta: number, time: number = 4000): void {
        this.stopBalanceEffect();

        if (delta < 0) {
            this.balanceEffectStyle.color = 'red';
        } else {
            this.balanceEffectStyle.color = 'green';
        }

        this.balanceDelta = parseFloat(toFixed(delta, 4));
        this.balanceEffectVisible = true;

        const that: DiceView = this;
        setTimeout(() =>  that.balanceEffectVisible = false, time);
    }

    public stopBalanceEffect(): void {
        if (this.balanceTicker !== null) {
            clearTimeout(this.balanceTicker);
            this.balanceTicker = null;
        }
    }
}


