














import {Component, Prop, Vue} from 'vue-property-decorator';
import {Scatter} from '../modules/eos/scatter';
import {Network} from '../modules/eos/network';
import {EOS} from '../modules/eos/eos';
import {Asset} from '../modules/eos/asset';
import {Token} from '../modules/eos/token';
import {Dice, RollMode} from '../modules/contract/dice/dice';
import {TransactionReceipt} from '../modules/eos/transactionReceipt';
import {Blackjack} from '../modules/contract/blackjack/blackjack';
import {BlackjackGame} from '../modules/contract/blackjack/blackjackGameTable';
import {Constants} from '../modules/eos/constants';

@Component
export default class FunctionTests extends Vue {
    public message: string = '';
    private eos = new EOS(new Scatter(Constants.NETWORK));
    private contract = new Blackjack(this.eos);
    private active: boolean = false;

    public async connect(): Promise<any> {
        if (this.eos.loggedIn()) {
            await this.eos.connector.logout();
        } else {
            await this.eos.connector.login();
            this.message = 'Hello ' + this.eos.connector.getAccount();
        }
    }

    public async getTable(): Promise<any> {
        const rows: any[] = await this.contract.gameTable.readAll();
        this.message = JSON.stringify(rows);
    }

    public async activate(): Promise<any> {
        const result: any = await this.contract.setActive(true);
        this.message = JSON.stringify(result);
    }

    public async init(): Promise<any> {
        const result: any = await this.contract.init();
        this.message = JSON.stringify(result);
    }

    // public async bet(): Promise<any> {
    //     const result: TransactionReceipt = await this.contract.play(
    //         RollMode.Over, 50, new Asset(1, Token.EOS),
    //     );
    //     console.log(JSON.stringify(result));
    // }

    public async hardClose(): Promise<any> {
        const row: BlackjackGame | undefined  = await this.contract.gameTable.getById(this.eos.account.name);
        const result: TransactionReceipt = await this.contract.hardClose(row!.id, '');
        this.message = JSON.stringify(result);
    }

    public async close(): Promise<any> {
        const result: TransactionReceipt = await this.contract.close(59);
        this.message = JSON.stringify(result);
    }
}


