





































































































import {Component, Vue} from 'vue-property-decorator';
import {DiceEffectCallback} from '../../modules/controllers/diceController';
import {BlackjackController} from '../../modules/controllers/blackjackController';
import {toFixed} from '../../modules/utils/commonFunctions';
import HistoryTableView from '@/views/blackjack/blackjackHistoryTableView.vue';
import CardListView from '@/views/blackjack/cardListView.vue';

@Component({
    components: {
        HistoryTableView,
        CardListView,
    },
})
export default class BlackjackView extends Vue implements DiceEffectCallback {
    public readonly control: BlackjackController = new BlackjackController(this);

    public balanceDelta: number = 0;
    public balanceTicker: any = null;
    public balanceEffectVisible: boolean = false;
    public balanceEffectStyle: any = {'color': 'green', 'margin-left': '10 px'};

    public mounted(): void {
        this.control.initialize();
    }

    public beforeDestroy(): void {
        this.control.destroy();
    }

    public startBalanceEffect(delta: number, time: number = 4000): void {
        this.stopBalanceEffect();

        if (delta < 0) {
            this.balanceEffectStyle.color = 'red';
        } else {
            this.balanceEffectStyle.color = 'green';
        }

        this.balanceDelta = parseFloat(toFixed(delta, 4));
        this.balanceEffectVisible = true;

        const that: BlackjackView = this;
        setTimeout(() =>  that.balanceEffectVisible = false, time);
    }

    public stopBalanceEffect(): void {
        if (this.balanceTicker !== null) {
            clearTimeout(this.balanceTicker);
            this.balanceTicker = null;
        }
    }
}

