import {BlackjackHistoryRow} from '../contract/blackjack/blackjackHistoryTable';
import {BlackjackGame} from '../contract/blackjack/blackjackGameTable';
import {Asset} from '../eos/asset';
import {Token} from '../eos/token';
import {EOS} from '../eos/eos';
import {Scatter} from '../eos/scatter';
import {Constants} from '../eos/constants';
import {Blackjack} from '../contract/blackjack/blackjack';
import {BlackJackStatus} from '../contract/blackjack/blackjackGame';
import {DiceEffectCallback} from '@/modules/controllers/diceController';
import {isEmptyString, isPresent} from '@/modules/utils/commonFunctions';
import {EOSAccountsTable} from '@/modules/eos/accountsTable';
import {TransactionReceipt} from '@/modules/eos/transactionReceipt';

export enum LoadingStatus {
    Initializing, NotLoading, Shuffling, Standing, Hitting,
}

export class BlackjackController {
    public normalTicker: any = undefined;
    public myGameTicker: any = undefined;
    public balanceTicker: any = undefined;
    public gameGlobals: any = undefined;
    public history: BlackjackHistoryRow[] = [];
    public historyLoading: boolean = true;
    public game: BlackjackGame = new BlackjackGame(undefined);

    public loadingStatus: LoadingStatus = LoadingStatus.Initializing;
    public amount: number = 1;
    public balance: Asset = new Asset(0, Token.EOS);

    private readonly eos = new EOS(new Scatter(Constants.NETWORK));
    private readonly blackjack = new Blackjack(this.eos);
    private readonly callback: DiceEffectCallback;

    public constructor(callback: DiceEffectCallback) {
        this.callback = callback;
    }

    public async initialize(): Promise<any> {
        this.startAllTick();
        this.startGetMyGame(0);
    }

    public hasStartGame(): boolean {
        return this.game.id > 0 && this.game.status === BlackJackStatus.Active;
    }

    public destroy(): void {
        this.stopAllTick();
    }

    public async loadMyGame(): Promise<any> {
        if (!this.eos.loggedIn()) {
            this.loadingStatus = LoadingStatus.NotLoading;
            throw new Error('Not logged in');
        }
        const game: BlackjackGame | undefined = await this.blackjack.gameTable.getById(this.eos.account.name);
        if (isPresent(game)) {
            if ((game!.status === this.game.status && game!.playerCards.length === this.game.playerCards.length) ||
                    game!.status === BlackJackStatus.Stood || game!.playerCards.length === 0) {
                throw new Error('Waiting for more game update');
            }
            this.game = game!;
        }
        this.loadingStatus = LoadingStatus.NotLoading;
    }

    public async loadHistory(): Promise<any> {
        this.history = await this.blackjack.historyTable.readAll();
        this.historyLoading = false;
    }

    public clearMyGameInfo(): void {
        this.game  = new BlackjackGame(undefined);
    }

    public async startGetMyGame(delay: number = 2000, txId: string = '', callback?: () => any): Promise<any> {
        if (!isEmptyString(txId)) {
            await this.eos.waitUntilImmutable(txId);
        }

        this.stopGetMyGame();
        this.myGameTicker = setTimeout(async () => {
            try {
                await this.loadMyGame();
                this.stopGetMyGame();

                if (isPresent(callback)) {
                    callback!();
                }
                await this.startGetMyBalance();
            } catch (error) {
                this.startGetMyGame(500, '', callback);
            }
        }, delay);
    }

    public stopGetMyGame() {
        if (this.myGameTicker !== null) {
            clearTimeout(this.myGameTicker);
            this.myGameTicker = null;
        }
    }


    public async updateBalance(): Promise<any> {
        if (this.eos.loggedIn()) {
            this.balance = await new EOSAccountsTable(this.eos, this.eos.account.name).getBalance();
        } else {
            this.balance = new Asset(0, Token.EOS);
        }
    }

    public async startGetMyBalance(): Promise<any> {
        if (!this.eos.loggedIn()) {
            return;
        }

        try {
            this.stopGetMyBalance();
            await this.updateBalance();
        } catch (error) {
            this.balanceTicker = setTimeout(() => {
                this.startGetMyBalance();
            }, 1000);
        }
    }

    public stopGetMyBalance(): void {
        if (this.balanceTicker !== null) {
            clearTimeout(this.balanceTicker);
            this.balanceTicker = null;
        }
    }

    public startAllTick() {
        this.stopAllTick();

        this.loadHistory();

        this.normalTicker = setTimeout(() => {
            this.startAllTick();
        }, 3000);
    }

    public stopAllTick() {
        if (this.normalTicker !== null) {
            clearTimeout(this.normalTicker);
            this.normalTicker = null;
        }
    }

    public async startGame(): Promise<any> {
        await this.eos.ensureConnected();

        try {
            this.loadingStatus = LoadingStatus.Shuffling;
            const receipt: TransactionReceipt =
                    await this.eos.transfer(this.blackjack.name, new Asset(this.amount, Token.EOS), 'new,');

            this.clearMyGameInfo();

            this.startGetMyGame(0, receipt.id, () => {
                this.loadingStatus = LoadingStatus.NotLoading;
            });
            this.startGetMyBalance();
        } catch (error) {
            this.loadingStatus = LoadingStatus.NotLoading;
        }
    }

    public async hit(): Promise<any> {
        try {
            this.loadingStatus = LoadingStatus.Hitting;

            const receipt: TransactionReceipt = await this.blackjack.hit();
            this.startGetMyGame(0, receipt.id, () => {
                this.loadingStatus = LoadingStatus.NotLoading;
            });
        } catch (error) {
            this.loadingStatus = LoadingStatus.NotLoading;
        }
    }

    public async stand(): Promise<any> {
        try {
            this.loadingStatus = LoadingStatus.Standing;

            const receipt: TransactionReceipt = await this.blackjack.stand();
            this.startGetMyGame(0, receipt.id, () => {
                this.loadingStatus = LoadingStatus.NotLoading;
            });
        } catch (error) {
            this.loadingStatus = LoadingStatus.NotLoading;
        }
    }

    public async insure(): Promise<any> {
    }

    public async double(): Promise<any> {
    }

    public isWaitingForAction(): boolean {
        return this.loadingStatus !== LoadingStatus.NotLoading;
    }

    public isStanding(): boolean {
        return this.loadingStatus === LoadingStatus.Standing;
    }

    public isHitting(): boolean {
        return this.loadingStatus === LoadingStatus.Hitting;
    }

    public isShuffling(): boolean {
        return this.loadingStatus === LoadingStatus.Shuffling;
    }

    public canStartNewGame(): boolean {
        return this.loadingStatus === LoadingStatus.NotLoading && this.game.status !== BlackJackStatus.Active;
    }

    public stepAmount(times: number): void {
        this.amount = this.amount * times;
    }

    public setMin(): void {
        this.amount = 0.01;
    }

    public setMax(): void {
        this.amount = this.balance.quantity;
    }

}
